body {
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.a-container {
  margin-right: 30px;
}

.a-container a {
  margin-right: 30px;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.a-container a:hover {
  color: #9f9f9f;
}

.bg-img {
  background-image: url(https://codingapple.com/wp-content/uploads/2022/04/bg.png);
  width: 100%;
  height: 200px;
  background-size: cover;
  background-position: center;
}

.product-grid img {
  width: 50%;
  height: 80%;
}

.article {
  margin: 0;
  padding: 0% 25%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: 26px;
}

.article .artile_btn {
  padding: 10px;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.article button {
  width: 1/2;
}

.Underline {
  position: absolute;
  bottom: -1px;
  left: 0;
  right: 0;
  height: 2px;
  background: black;
}
