.cart {
  width: 80%;
}

.cart .head {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 0.3fr 1.5fr 1fr 1fr;
      grid-template-columns: 0.3fr 1.5fr 1fr 1fr;
}

.cart .content {
  width: 100%;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 0.3fr 1.5fr 1fr 1fr;
      grid-template-columns: 0.3fr 1.5fr 1fr 1fr;
}
